html {
  scroll-behavior: smooth;
  background-color: #0e101c;
}

* {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
}

.logo img {
  width: 100%;
  max-width: 10em;
}

nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  font-size: 1.25em;
}

nav li {
  margin-right: 1em;
}

nav a {
  text-decoration: none;
  color: white;
  font-size: 1em;
  margin: 2rem;
  padding: 1em;
}

#contactinfo {
  background-color: #337ab8;
  border-radius: 1rem;
}

.grid {
  display: grid;
  grid-template-areas:
    "head head"
    "about img"
    "about main"
    "hr hr"
    "contact button";
  grid-gap: 1rem;
  padding: 1.5rem;
}

.head {
  grid-area: head;
}

.card {
  grid-area: about;
  background-color: #141729;
  border-radius: 5px;
  color: white;
}

.card h1 {
  padding: 0.3em;
}

.card p {
  padding: 1em;
  color: #c2c3c7;
  font-size: large;
}

.office {
  grid-area: img;
}

.office img {
  width: 100%;
  min-width: 100%;
  border-radius: 0.2rem;
}

.main {
  grid-area: main;
}

.main h1 {
  color: white;
}

.main p {
  margin-top: 2rem;
  color: #c2c3c7;
  font-size: 1em;
}

.hr {
  grid-area: hr;
}

.contactbtn {
  grid-area: contact;
  color: #c2c3c7;
}

.contactbtn h1 {
  color: white;
  margin-bottom: 1rem;
}

.contactbtn p {
  width: 70%;
}

.button {
  margin-top: 2rem;
  @media screen and (min-width: 768px) {
    display: grid;
    place-content: center;
  }
}

.button a {
  grid-area: button;
  padding: 1rem 1rem;
  border-radius: 1rem;
  text-decoration: none;
  color: #fff;
  font-weight: bold;
  background-color: #337ab8;
}

a:hover {
  color: #c2c3c7;
}

@media (max-width: 768px) {
  .grid {
    grid-template-areas:
      "head"
      "about"
      "img"
      "main"
      "hr"
      "contact"
      "button";
  }
}

@media screen and (max-width: 768px) {
  nav {
    display: none;
  }

  .logo img {
    display: none;
  }
}